.box1{
  margin-top: 25px;
  height: 360px;
  display: inline-block;

  .banner{
    width: 1200px;
    display: inline-block;

    .slick-slider{

      img{
        display: inline-block;
        width: 100%;
        height: auto;
      }
    }
  }
}

.box2{
  margin-top: 15px;
  padding-bottom: 20px;
  .gameBox{
    overflow: hidden;
    position: relative;
    color: #fff;

    .content{
      position: absolute;
      top: 60px;
      left: 60px;
      z-index: 999;
      
      .title{
        font-size: 22px;
        font-weight: bold;
      }
      
      .text{
        font-size: 14px;
      }
      
      a{
        font-size: 13px;
        padding: 6px 20px;
        border: 1px solid #fff;
        color: #fff;
        border-radius: 4px;
        margin-top: 12px;
        display: inline-block;
      }
    }
    img{
      width: 100%;
    }
  }
  .gameBox:hover{
    img{
      transform: rotate(3deg) scale(1.2);
      transition: all .5s ease;
    }
  }
}

.box3{
  background: rgba(0,0,0,0.5);

  .app{
    padding: 30px 0 12px;
    color: #aeaeae;
    font-size: 15px;
    font-weight: 500;
    border-bottom: 1px solid rgba(255,255,255,0.1);
  }

  .content{
    display: inline-block;
    font-size: 13px;
    color: #fff;
    line-height: 28px;
    vertical-align: top;

    .title{
      color: #ffa200;
      font-size: 15px;
      margin-top: 18px;
      font-weight: 500;
      margin-bottom: 8px;
    }
  }

  .qr{
    display: inline-block;
    background: url("../../assets/img/home/code-bg.png");
    width: 141px;
    height: 136px;
    vertical-align: top;
    margin-top: 22px;
    margin-left: 60px;

    img{
      width: 141px;
      padding: 6px 9px;
    }
  }

  .copyright{
    font-size: 12px;
    color: #fff;
    line-height: 24px;
    margin-top: 20px;
    padding-bottom: 20px;
  }

  .right{
    float: right;
  }
}





