//============================
//HEADER头部
//============================

.header{

  .top{
    height: 40px;
    line-height: 40px;
    background: #3066AE;
    color: #fff;
    font-size: 12px;

    .left{
      display: inline-block;

      i{
        margin-right: 8px;
        font-size: 14px;
      }

      .announce{
        display: inline-block;
        width: 200px;
      }
      a{
        color: #fff;
        padding-right: 5px;
      }
      .announce_title{
        display: inline-block;
        vertical-align: top
      }
    }

    .right{

      display: inline-block;
      float: right;

      li.topUserInfo{
        display: inline-block;
        padding:0 10px;

        .top_btn{
          color: #fff;

          .caret-down{
            font-size: 13px;
          }
        }
        div{
          display: inline-block;
          margin-left: 8px;
          margin-right: 10px;

          .money{
            padding: 0 5px;
          }

          i{
            font-size: 14px;
          }
        }

        i{
          font-size: 16px;
          vertical-align: text-top;
          padding: 0 5px;
        }
      }
      li:hover{
        background: rgba(0, 0, 0, 0.4);
        .money{
          color: #fff;
        }
      }
    }
  }


  .navbar{
    background: #142346;
    height: 60px;
    position: relative;

    .left{
      display: inline-block;
      margin: 12px 0;

      .logo{
        height: 36px;
      }
    }

    .right{
      display: inline-block;
      float: right;

      i{
        margin-left: 5px;
      }

      a{
        display: inline-block;
        line-height: 60px;
        padding: 0px 20px;
        font-size: 13px;
        color: #fff;
        position: relative;

        .hover{
          position: absolute;
          width: 100%;
          top: 0;
          left: 0;
          height: 60px;
          display: none;
          text-align: center;
          line-height: 60px;
          background: #0e1933;

          img{
            width: 22px;
          }
        }

        .caret-down{
          font-size: 12px;
          padding: 0 2px;
          transform: scale(0.9);
          color: #a5a5a5;
        }
      }

      a:hover{
        .hover{
          display: block;
        }
      }
    }
  }
}


//============================
//彩票下拉菜单
//============================


.lotteryMenu{
  top: 90px !important;

  .game_list_box{
    border-bottom: 1px solid #eee;
    border-left: 2px solid #fff;
    border-right: 2px solid #fff;

    .title{
      margin-top: 17px;
      color: #999;
      padding-left: 10px;
      width: 100px;
      display: inline-block;
      vertical-align: top;
      font-size: 13px;

    }

    .game_list{
      display: inline-block;
      vertical-align: top;
      width: 630px;
      margin-top: 12px;

      .list{
        padding-bottom: 2px;
      }
    }

    a{
      padding: 5px;
      border: 1px solid rgba(0,0,0,0.1);
      display: inline-block;
      margin: 0 10px 10px 0;
      font-size: 12px;
      width: 94px;
      text-align: center;
    }
    a:hover{
      border-color: #24abe7;
      color: #24abe7;
    }
  }

  .game_list_box:last-child{
    border-bottom: 0;
  }
  
  .game_list_box:nth-child(odd){
    background: #f9f9f9;
  }

  .game_list_box:hover{
    background: #f5f6f7;
    border-left: 2px solid #22abe7;
  }
}


//============================
//个人中心下拉菜单
//============================
.userCenter{
  top: 31px !important;
}
.account_center{
  font-size: 13px;

  li{
    padding: 10px 0;
    .left{
      display: inline-block;
      width: 120px;
      border-right: 1px solid #eee;
      padding-left: 10px;
      vertical-align: top;
      line-height: 18px;
      margin: 9px 0;
    }

    .right{
      display: inline-block;
      padding-left: 16px;
      min-width: 460px;
      max-width: 500px;
      vertical-align: top;

      a{
        line-height: 36px;
        margin: 0 15px;
        display: inline-block;
        border-bottom: 1px solid #fff0;
        color: #666;
      }

      a:hover{
        color: #ff4040;
        border-bottom: 1px solid #ff4040;
      }
    }
  }

  li:nth-child(even){
    background: #705d8c08;
    border-top: 1px solid #f1f1f1;
    border-bottom: 1px solid #f1f1f1;
  }

  i{
    font-size: 16px;
    color: #ff4040;
    margin-right: 10px;
  }

}



//============================
//FOOTER页面底部样式
//============================


.footer{
  background: #262732;
  width: 100%;
  color: #fff;

  .content{
    background: #212737;
    height: 120px;
    .left{
      display: inline-block;
      margin-top: 30px;
      float: right;

      div{
        display: inline-block;
        margin-right: 30px;

        p{
          font-size: 12px;
          color: rgba(255,255,255,0.5);
        }
        img{
          margin-top: 20px;
          height: 28px;
        }
      }
    }


    .right{
      float: left;
      text-align: left;
      margin: 17px 0;

      p{
        font-size: 12px;
        margin-top: 10px;

        span{
          color: #cbe0ef;
        }
      }
    }
  }
}


//============================
//右侧悬浮导航样式
//============================


.side{
  position: fixed;
  right: 10px;
  top: 30%;
  text-align: center;

  ul{
    width: 44px;

    li{
      background: #1890ff;
      border-bottom: 0.5px solid rgba(255,255,255,0.1);

      a{
        font-size: 12px;
        color: #fff;
        padding: 10px 0;
        display: block;
      }

      .button{
        font-size: 12px;
        color: #fff;
        padding: 10px 0;
        display: block;
      }

      i{
        font-size: 18px;
        display: block;
        margin-bottom: 5px;
      }

      .ant-back-top{
        position: inherit;
        color: #fff;
        padding: 10px 0;
        height: auto;
        width: auto;
        font-size: 12px;
      }
    }
    li:last-child{
      border-bottom: 0;
    }
    li:hover{
      background: #1386ef;
    }
  }
}


//============================
//内页导航样式
//============================


.sidemenu{
  position: absolute;
  left: 0px;
  background: #1890ff;

  .title{
    padding: 0 15px;
    font-size: 14px;
    font-weight: bold;
    color: #333;
    background: #f5f5f5;
    line-height: 60px;
    border-bottom: 1px solid #eee;

    i{
      margin-right: 10px;
    }
  }

  .ant-menu-submenu-title{
    padding-left: 15px!important;
    span{
      font-size: 13px;
    }
  }

  a{
    font-size: 13px;
  }

  .ant-menu-inline{
    border-color: #f5f5f5;

    li{

      .ant-menu-item{
        padding-left: 42px!important;
        font-size: 13px;
      }
    }
  }

  .anticon{
    font-size: 16px!important;
  }

}




.S_header{
  background: #1e2331;
  height: 64px;

  .left{
    display: inline-block;
    margin: 12px 0;

    img{
      height: 40px;
    }
  }

  .right{
    float: right;
    margin: 16px 0;

    button{
      font-size: 13px;
      margin-left: 10px;
    }
  }
}



.appDownload{
  padding: 30px 0 15px;
  text-align: center;

  img{
    width: 180px;
  }

  p{
    font-size: 16px;
    font-weight: bold;
    color: #333;
    margin-top: 15px;
    margin-bottom: 5px;
  }

  span{
    color: #666;
    font-size: 13px;
  }
}


