.announce{

    .title{
      text-align: left;
      padding-left: 20px;
    }
    .time{
      width: 200px;
    }
    tbody{
      tr{
        td{
          height: 60px!important;
        }
        td:first-child{
          text-align: left;
          padding-left: 20px;
          font-size: 13px;
        }
      }
    }
  }
  
  
  .announce_content{
  
    .title{
      background: #f5f6f7;
      border-top: 1px solid #eee;
      padding: 20px 0 15px;
      text-align: center;
  
      p:nth-child(1){
        font-size: 22px;
        font-weight: bold;
      }
      p:nth-child(2){
        font-size: 12px;
        color: #aaa;
        margin-top: 4px;
      }
    }
  
    .content{
      font-size: 13px;
      line-height: 28px;
      padding: 30px 70px 50px;
    }
  }